import React, { useEffect } from 'react';
import useValidation from '../hooks/useValidation';
import PopupWithForm from './PopupWithForm';

const AddPlacePopup = ({ isOpen, onClose, onAddPlace, onOverlayClick, isLoading }) => {
  //  
  const { values, errors, isValid, handleChange, resetForms } = useValidation('.form');
  //     
  useEffect(() => {
    resetForms();
  }, [isOpen, resetForms]);
  //   
  function handleSubmit(evt) {
    evt.preventDefault();
    onAddPlace(values);
  }

  return (
    <PopupWithForm
      name="new-card-form"
      title=" "
      btnTitle={!isLoading ? '' : '...'}
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      onOverlayClick={onOverlayClick}
      isValid={isValid}
    >
      <input
        id="title"
        name="name"
        type="text"
        className="form__input"
        placeholder=""
        aria-label=""
        required
        minLength="2"
        maxLength="30"
        onChange={handleChange}
        value={values.name || ''}
      />
      <span className="form__error-message title-error">{errors.name}</span>
      <input
        id="link"
        name="link"
        type="url"
        className="form__input"
        placeholder="  "
        aria-label="  "
        required
        onChange={handleChange}
        value={values.link || ''}
      />
      <span className="form__error-message link-error">{errors.link}</span>
    </PopupWithForm>
  );
};

export default AddPlacePopup;
