import React, { useEffect, useRef } from 'react';
import useValidation from '../hooks/useValidation';
import PopupWithForm from './PopupWithForm';

const EditAvatarPopup = ({ isOpen, onClose, onUpdateAvatar, onOverlayClick, isLoading }) => {
  //  
  const { values, errors, isValid, handleChange, resetForms } = useValidation('.form');

  /////////////////////////////////////////////
  //    Ref             //
  /////////////////////////////////////////////

  // const avatarUrlRef = useRef();
  // //   
  // function handleSubmit(evt) {
  //   evt.preventDefault();
  //   onUpdateAvatar({
  //     avatar: avatarUrlRef.current.value,
  //   });
  // }
  // //     
  // useEffect(() => {
  //   avatarUrlRef.current.value = '';
  // }, [isOpen]);

  //     
  useEffect(() => {
    resetForms();
  }, [isOpen, resetForms]);
  //   
  function handleSubmit(evt) {
    evt.preventDefault();
    onUpdateAvatar(values);
  }

  return (
    <PopupWithForm
      name="new-avatar-form"
      title=" "
      btnTitle={!isLoading ? '' : '...'}
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      onOverlayClick={onOverlayClick}
      isValid={isValid}
    >
      <input
        id="avatar"
        name="avatar"
        type="url"
        className="form__input"
        placeholder="  "
        aria-label="  "
        // ref={avatarUrlRef}
        value={values.avatar || ''}
        onChange={handleChange}
        required
      />
      <span className="form__error-message avatar-error">{errors.avatar}</span>
    </PopupWithForm>
  );
};

export default EditAvatarPopup;
