import React, { useContext, useEffect } from 'react';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import useValidation from '../hooks/useValidation';
import PopupWithForm from './PopupWithForm';

const EditProfilePopup = ({ isOpen, onClose, onUpdateUser, onOverlayClick, isLoading }) => {
  //        
  const currentUser = useContext(CurrentUserContext);
  //  
  const { values, errors, isValid, handleChange, resetForms } = useValidation('.form');
  //    
  useEffect(() => {
    currentUser && resetForms(currentUser, {}, true);
  }, [currentUser, isOpen, resetForms]);
  //    
  function handleSubmit(evt) {
    evt.preventDefault();
    onUpdateUser(values);
  }

  return (
    <PopupWithForm
      name="profile-form"
      title=" "
      btnTitle={!isLoading ? '' : '...'}
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      onOverlayClick={onOverlayClick}
      isValid={isValid}
    >
      <input
        id="name"
        name="name"
        type="text"
        className="form__input"
        placeholder=""
        aria-label=""
        required
        minLength="2"
        maxLength="40"
        onChange={handleChange}
        value={values.name || ''}
      />
      <span className="form__error-message name-error">{errors.name}</span>
      <input
        id="about"
        name="about"
        type="text"
        className="form__input"
        placeholder=""
        aria-label=""
        required
        minLength="2"
        maxLength="200"
        onChange={handleChange}
        value={values.about || ''}
      />
      <span className="form__error-message about-error">{errors.about}</span>
    </PopupWithForm>
  );
};

export default EditProfilePopup;
